   /* Header.css */

.header {
  position: sticky;
  display: flex;
  justify-content: space-between;
  align-items: center;
  
  color: #fff;
  padding: 20px;
  background-color: #1976d2;
  height: 10vh;
}
.brand h1 {
  margin: 10px;
  font-size: 1.5rem;
  cursor: pointer;
}

.nav {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.nav ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav li {
  margin: 0 15px;
  cursor: pointer;
  font-weight: 500;
    font-size: 0.875rem;
    line-height: 1.25;
    letter-spacing: 0.02857em;
    text-transform: uppercase;
}
.nav li:hover{
  color:#cc5500 ;
}

.menu-btn {
  display: none;
}

/* .menu-btn__burger {
  width: 30px;
  height: 2px;
  background-color: #fff;
  transition: 0.3s;
} */

.nav.open {
  display: block;
}

/* .menu-btn.open .menu-btn__burger {
  transform: rotate(45deg);
}

.menu-btn.open .menu-btn__burger::before,
.menu-btn.open .menu-btn__burger::after {
  top: 0;
  transform: rotate(90deg);
} */

.menu-btn.open .menu-btn__burger::before {
  margin-top: 8px;
}

.menu-btn.open .menu-btn__burger::after {
  margin-top: -8px;
}

@media (max-width: 768px) {
  .brand h1 {
    font-size: 1.2rem;
  }

  .nav {
    display: none;
    flex-direction: column;
    align-items: flex-start;
    position: absolute;
    top: 60px;
    left: 0;
    width: 100%;
    background-color: #1976d2;
  }

  .nav.open ul {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  .nav li {
    margin: 15px 0;

  }

  .menu-btn {
    display: block;
    cursor: pointer;
  }
}
